// Minimal frame representation (no UI tree) built via SAX.
data class SmlFrame(
    val name: String,
    val props: MutableMap<String, PropertyValue> = LinkedHashMap(),
    val children: MutableList<SmlFrame> = mutableListOf()
)

sealed interface ParentScope {
    data object None : ParentScope
    data class Row(val scope: RowScope) : ParentScope
    data class Column(val scope: ColumnScope) : ParentScope
}

typealias SmlRenderer = @Composable (frame: SmlFrame, parent: ParentScope, renderChildren: @Composable (ParentScope) -> Unit) -> Unit

class SmlRenderRegistry {
    private val map = mutableMapOf<String, SmlRenderer>()
    fun register(name: String, renderer: SmlRenderer) { map[name] = renderer }
    fun resolve(name: String): SmlRenderer? = map[name]
}

fun defaultSmlRegistry(openUrl: (String) -> Unit = {}): SmlRenderRegistry = SmlRenderRegistry().apply {
    // Page -> Column with padding (padding accepts "32", "32 16", "32 16 16 16")
    register("Page") { frame, _, renderChildren ->
        val pad = paddingValues(frame.props, defaultAll = 16)
        Column(
            modifier = Modifier.fillMaxSize().padding(pad),
            verticalArrangement = Arrangement.spacedBy(16.dp)
        ) {
            renderChildren(ParentScope.Column(this))
        }
    }
    // Row
    register("Row") { frame, _, renderChildren ->
        val spacing = frame.props.int("spacing") ?: 0
        val pad = paddingValues(frame.props, defaultAll = 0)
        Row(
            modifier = Modifier.fillMaxWidth().padding(pad),
            horizontalArrangement = Arrangement.spacedBy(spacing.dp),
            verticalAlignment = Alignment.CenterVertically
        ) {
            renderChildren(ParentScope.Row(this))
        }
    }
    // Column
    register("Column") { frame, _, renderChildren ->
        val spacing = frame.props.int("spacing") ?: 0
        val pad = paddingValues(frame.props, defaultAll = 0)
        val align = when (frame.props.string("alignment")) {
            "center" -> Alignment.CenterHorizontally
            else -> Alignment.Start
        }
        Column(
            modifier = Modifier.fillMaxWidth().padding(pad),
            verticalArrangement = Arrangement.spacedBy(spacing.dp),
            horizontalAlignment = align
        ) {
            renderChildren(ParentScope.Column(this))
        }
    }
    // Label/Text
    val textRenderer: SmlRenderer = { frame, _, _ -> Text(frame.props.string("text") ?: "") }
    register("Label", textRenderer)
    register("Text", textRenderer)
    // Link
    register("Link") { frame, _, _ ->
        val label = frame.props.string("text").orEmpty()
        val href = frame.props.string("href").orEmpty()
        ClickableText(
            text = AnnotatedString(label),
            style = MaterialTheme.typography.bodyMedium.copy(
                color = MaterialTheme.colorScheme.primary,
                textDecoration = TextDecoration.Underline
            )
        ) { openUrl(href) }
    }
    // Markdown (bold/italic/heading, link click callback)
    register("Markdown") { frame, _, _ ->
        val raw = frame.props.string("text") ?: ""
        val (heading, body) = stripHeading(raw)
        val annotated: AnnotatedString = parseInlineMarkdown(body)
        val style = when (heading) {
            1 -> MaterialTheme.typography.headlineLarge
            2 -> MaterialTheme.typography.headlineMedium
            3 -> MaterialTheme.typography.headlineSmall
            else -> MaterialTheme.typography.bodyMedium
        }.copy(color = MaterialTheme.colorScheme.onBackground)
        ClickableText(text = annotated, style = style) { offset ->
            annotated.getStringAnnotations("URL", offset, offset).firstOrNull()?.let { openUrl(it.item) }
        }
    }
    // Grid (fallback: stacked columns with spacing)
    register("Grid") { frame, _, renderChildren ->
        val spacing = frame.props.int("spacing") ?: 8
        Column(
            modifier = Modifier.fillMaxWidth(),
            verticalArrangement = Arrangement.spacedBy(spacing.dp)
        ) { renderChildren(ParentScope.Column(this)) }
    }
    // Spacer variants with scope-aware sizing
    register("Spacer") { frame, parent, _ ->
        val amount = frame.props.int("amount") ?: 0
        when (parent) {
            is ParentScope.Row -> Spacer(Modifier.width(amount.dp))
            is ParentScope.Column -> Spacer(Modifier.height(amount.dp))
            else -> Spacer(Modifier.height(amount.dp))
        }
    }
    register("VSpacer") { frame, parent, _ ->
        val amount = frame.props.int("amount") ?: 0
        when (parent) {
            is ParentScope.Column -> Spacer(Modifier.height(amount.dp))
            else -> Spacer(Modifier.height(amount.dp))
        }
    }
    register("HSpacer") { frame, parent, _ ->
        val amount = frame.props.int("amount") ?: 0
        when (parent) {
            is ParentScope.Row -> Spacer(Modifier.width(amount.dp))
            else -> Spacer(Modifier.width(amount.dp))
        }
    }
    // Button
    register("Button") { frame, parent, renderChildren ->
        val txt = frame.props.string("text")
        Button(onClick = { console.log("action: ${frame.props.string("action")}") }) {
            if (txt != null) Text(txt)
            renderChildren(parent)
        }
    }
}

// Helpers
private fun Map<String, PropertyValue>.string(key: String): String? =
    (this[key] as? PropertyValue.StringValue)?.value
private fun Map<String, PropertyValue>.int(key: String): Int? =
    (this[key] as? PropertyValue.IntValue)?.value
private fun Map<String, PropertyValue>.float(key: String): Float? =
    (this[key] as? PropertyValue.FloatValue)?.value

private fun paddingValues(props: Map<String, PropertyValue>, defaultAll: Int): androidx.compose.foundation.layout.PaddingValues {
    props.int("padding")?.let { return androidx.compose.foundation.layout.PaddingValues(it.dp) }
    val raw = props.string("padding")?.trim() ?: return androidx.compose.foundation.layout.PaddingValues(defaultAll.dp)
    val parts = raw.split(Regex("\\s+")).filter { it.isNotBlank() }.mapNotNull { it.toIntOrNull() }
    return when (parts.size) {
        1 -> androidx.compose.foundation.layout.PaddingValues(parts[0].dp)
        2 -> androidx.compose.foundation.layout.PaddingValues(horizontal = parts[1].dp, vertical = parts[0].dp)
        4 -> androidx.compose.foundation.layout.PaddingValues(
            start = parts[3].dp,
            top = parts[0].dp,
            end = parts[1].dp,
            bottom = parts[2].dp
        )
        else -> androidx.compose.foundation.layout.PaddingValues(defaultAll.dp)
    }
}

// SAX  Frame tree
private class ComposeBuildingHandler(
    private val registry: SmlRenderRegistry
) : SmlHandler {
    private val stack = ArrayDeque<SmlFrame>()
    private val roots = mutableListOf<SmlFrame>()

    override fun startElement(name: String) {
        stack.addLast(SmlFrame(name))
    }

    override fun onProperty(name: String, value: PropertyValue) {
        val cur = stack.lastOrNull() ?: throw SmlParseException("Property outside of element", Span(0,0,0))
        cur.props[name] = value
    }

    override fun endElement(name: String) {
        val f = stack.removeLastOrNull() ?: throw SmlParseException("Mismatched end '$name'", Span(0,0,0))
        if (f.name != name) throw SmlParseException("Expected end of ${f.name}, got $name", Span(0,0,0))
        if (stack.isEmpty()) roots += f else stack.last().children += f
    }

    fun frames(): List<SmlFrame> = roots
}

private fun buildComposable(text: String, registry: SmlRenderRegistry): @Composable () -> Unit {
    val h = ComposeBuildingHandler(registry)
    SmlSaxParser(text).parse(h)
    val frames = h.frames()
    return { frames.forEach { renderFrame(it, ParentScope.None, registry) } }
}

@Composable
private fun renderFrame(frame: SmlFrame, parent: ParentScope, registry: SmlRenderRegistry) {
    val renderer = registry.resolve(frame.name) ?: { _, p, renderChildren -> renderChildren(p) }
    renderer(frame, parent) { nextParent ->
        frame.children.forEach { child -> renderFrame(child, nextParent, registry) }
    }
}

@Composable
fun RenderSml(text: String, registry: SmlRenderRegistry = defaultSmlRegistry()) {
    val node = remember(text, registry) { mutableStateOf(buildComposable(text, registry)) }
    node.value()
}
